import React from 'react';
import { Enums as csEnums } from '@cornerstonejs/core';
import PropTypes from 'prop-types';
import { ViewportImplantForm } from '@ohif/ui';
import { segmentation as cstSegmentation, Types as cstTypes } from '@cornerstonejs/tools';
import { getAxis, findStart, fillCylinder, fillSTL } from './implantUtils';
import implantT3ST410 from './implants/T3ST410.json';

import vtkDataArray from '@kitware/vtk.js/Common/Core/DataArray';
import vtkImageData from '@kitware/vtk.js/Common/DataModel/ImageData';
import vtkMapper from '@kitware/vtk.js/Rendering/Core/Mapper';
import vtkActor from '@kitware/vtk.js/Rendering/Core/Actor';
import vtkImageMarchingCubes from '@kitware/vtk.js/Filters/General/ImageMarchingCubes.js';

function waitCondition<T>(condition: () => T) {
  return new Promise<T>((resolve, reject) => {
    const wait = () => {
      const result = condition();
      if (result) {
        return resolve(result);
      } else {
        setTimeout(() => wait(), 50);
      }
    };
    wait();
  });
}

const CornerstoneViewportImplantForm = ({ onClose, servicesManager }) => {
  const displayImplantIn3DViewport = async (
    implantMetadata,
    toothNumber: number,
    imageData: vtkImageData,
    voxelManager
  ) => {
    const {
      cornerstoneCacheService,
      cornerstoneViewportService,
      hangingProtocolService,
      segmentationService,
    } = servicesManager.services;
    const { data: implantData, dims: implantDimensions, start } = implantMetadata;

    const implantImageData = vtkImageData.newInstance();
    implantImageData.setDimensions(implantDimensions);
    const origin = imageData.indexToWorld(start);
    implantImageData.setOrigin([origin[0], origin[1], origin[2]]);
    implantImageData.setDirection(imageData.getDirection());
    implantImageData.setSpacing(imageData.getSpacing());
    const implantDataArray = vtkDataArray.newInstance({
      numberOfComponents: voxelManager.numberOfComponents,
      values: implantData,
      name: '',
    });
    implantImageData.getPointData().setScalars(implantDataArray);

    // TODO replace with native segmentation representation
    const polyExtractor = vtkImageMarchingCubes.newInstance({
      contourValue: 1,
      computeNormals: false,
      mergePoints: true,
    });
    polyExtractor.setInputData(implantImageData);
    const polySTL = polyExtractor.getOutputData();
    polySTL.buildLinks();

    const segmentation: cstTypes.Segmentation = segmentationService.getGeneratedSegmentation();
    const { segmentIndex: implantIdx } = Object.values(segmentation.segments).find(
      segment => segment?.label === 'Implant'
    );

    const color = segmentationService.getSegmentColor(
      'default',
      segmentation.segmentationId,
      implantIdx
    );

    const mapper = vtkMapper.newInstance();
    mapper.setInputData(polySTL);
    const actor = vtkActor.newInstance();
    actor.setMapper(mapper);
    actor.getProperty().setColor(color[0] / 255, color[1] / 255, color[2] / 255);
    actor.getProperty().setOpacity(0.9);
    actor.getProperty().setBackfaceCulling(false);

    await waitCondition(
      () => hangingProtocolService.getActiveProtocol().protocol?.id === 'mprAnd3DVolumeViewport'
    );
    const volumeViewportId = await waitCondition(() => {
      const viewportIds = cornerstoneViewportService.getViewportIds();
      const viewportId = viewportIds.find(
        vpId =>
          cornerstoneViewportService.getViewportInfo(vpId).getViewportType() ===
          csEnums.ViewportType.VOLUME_3D
      );
      return viewportId;
    });

    const volumeViewport = cornerstoneViewportService.getCornerstoneViewport(volumeViewportId);
    const actorEntry = { uid: `poly_${implantIdx}_${toothNumber}`, actor: actor };
    volumeViewport.addActor(actorEntry);
    const surfaceActorEntries = cornerstoneCacheService.getSurfaceActorEntries();
    cornerstoneCacheService.setSurfaceActorEntries([...surfaceActorEntries, actorEntry]);
  };

  const addImplant = (toothNumber, implant) => {
    const { viewportGridService, segmentationService, panoramicService } = servicesManager.services;

    const { segments, segmentationId } = segmentationService.getGeneratedSegmentation();
    const { voxelManager, dimensions, spacing, imageData } =
      segmentationService.getLabelmapVolume(segmentationId);

    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    const panoramicState = panoramicService.getPanoramicState(displaySetUID);
    const { teethAxes, minMaxTissueLayer } = panoramicState;

    const axis = getAxis(segments, teethAxes, toothNumber); // [vz, vy, vx, z0, y0, x0]
    if (!axis) {
      return false;
    }
    const tStart = findStart(voxelManager, dimensions, toothNumber, axis, minMaxTissueLayer);
    if (tStart === undefined) {
      return false;
    }

    if (!implant) {
      fillCylinder(voxelManager, dimensions, toothNumber, axis, tStart, spacing);
    } else {
      const implantMetadata = fillSTL(voxelManager, toothNumber, axis, tStart, spacing, implant);
      displayImplantIn3DViewport(implantMetadata, toothNumber, imageData, voxelManager);
    }

    panoramicService.copySegmentationVolumesForDentascan();
    cstSegmentation.triggerSegmentationEvents.triggerSegmentationDataModified(segmentationId);

    onClose();
    return true;
  };

  return (
    <ViewportImplantForm
      onClose={onClose}
      addImplant={(toothNumber: number) => addImplant(toothNumber, implantT3ST410)}
    />
  );
};

CornerstoneViewportImplantForm.propTypes = {
  onClose: PropTypes.func.isRequired,
  servicesManager: PropTypes.object.isRequired,
};

export default CornerstoneViewportImplantForm;
