import React from 'react';
import {
  WrappedPanelStudyBrowser,
  Edition3DPanel,
  PanoramicGenerationPanel,
} from './Panels';
import i18n from 'i18next';

// TODO:
// - No loading UI exists yet
// - cancel promises when component is destroyed
// - show errors in UI for thumbnails if promise fails

function getPanelModule({ commandsManager, extensionManager, servicesManager }) {
  return [
    {
      name: 'seriesList',
      iconName: 'tab-studies',
      iconLabel: 'Studies',
      label: i18n.t('SidePanel:Studies'),
      component: props => (
        <WrappedPanelStudyBrowser
          {...props}
          commandsManager={commandsManager}
          extensionManager={extensionManager}
          servicesManager={servicesManager}
        />
      ),
    },
    {
      name: 'volumeEditionPanel',
      iconName: 'edition-3D',
      iconLabel: '3D Edition',
      label: '3D Edition',
      component: Edition3DPanel.bind(null, {
        commandsManager,
        extensionManager,
        servicesManager,
      }),
    },
    {
      name: 'panoramicGenerationPanel',
      iconName: 'panoramic',
      iconLabel: 'Panoramic Generation',
      label: 'Panoramic',
      component: PanoramicGenerationPanel.bind(null, {
        commandsManager,
        extensionManager,
        servicesManager,
      }),
    },
  ];
}

export default getPanelModule;
