import React, { useMemo, useEffect, useRef, useState, useCallback } from 'react';
import { Button, Icon, useViewportGrid, useImageViewer } from '@ohif/ui';
import { CheckBox } from '@ohif/ui';
import vtkMapper from '@kitware/vtk.js/Rendering/Core/Mapper';
import vtkVolumeMapper from '@kitware/vtk.js/Rendering/Core/VolumeMapper';
import vtkActor from '@kitware/vtk.js/Rendering/Core/Actor';
import vtkVolume from '@kitware/vtk.js/Rendering/Core/Volume';
import vtkImageMarchingCubes from '@kitware/vtk.js/Filters/General/ImageMarchingCubes.js';
import { getEnabledElement, utilities as csUtils } from '@cornerstonejs/core';
import vtkOutlineFilter from '@kitware/vtk.js/Filters/General/OutlineFilter';
import vtkImageData from '@kitware/vtk.js/Common/DataModel/ImageData';
import vtkImageCropFilter from '@kitware/vtk.js/Filters/General/ImageCropFilter';
import vtkImageReslice from '@kitware/vtk.js/Imaging/Core/ImageReslice';
import vtkDataArray from '@kitware/vtk.js/Common/Core/DataArray';
import vtkPolyData from '@kitware/vtk.js/Common/DataModel/PolyData';
import PolyDataExtractor from './Edition3DUtils/extractCellGroupsFromPolydata.js';
import QuadricDecimator from './Edition3DUtils/quadricDecimation.js';
import vtkSTLWriter from '@kitware/vtk.js/IO/Geometry/STLWriter';
import vtkSTLReader from '@kitware/vtk.js/IO/Geometry/STLReader';
import { FormatTypes } from '@kitware/vtk.js/IO/Geometry/STLWriter/Constants.js';
import vtkColorTransferFunction from '@kitware/vtk.js/Rendering/Core/ColorTransferFunction.js';
import vtkPiecewiseFunction from '@kitware/vtk.js/Common/DataModel/PiecewiseFunction.js';
import vtkWindowedSincPolyDataFilter from '@kitware/vtk.js/Filters/General/WindowedSincPolyDataFilter';

// TODO Optimized code for marchingCubes
import imageMarchingCubes from './Edition3DUtils/ImageMarchingCubes.js';
const _ = require('lodash');

const seededRandom = (seed: number) => {
  let value = seed % 2147483647;
  return () => {
    value = (value * 16807) % 2147483647;
    return value / 2147483647;
  };
};
const myRandom = seededRandom(42);

export default function Edition3DPanel({
  commandsManager,
  extensionManager,
  servicesManager,
}): React.FunctionComponent {
  const [percentileValues, setPercentileValues] = useState(new Array(100).fill(0));
  const [cTPercentile, setCTPercentile] = useState('50');
  const [ctValue, setCtValue] = useState('500');
  const [reductionPercentile, setReductionPercentile] = useState('50');
  const [skinThreshold, setSkinThreshold] = useState('20');
  const [boneThreshold, setBoneThreshold] = useState('450');
  const [toothThreshold, setToothThreshold] = useState('1200');
  const isFirstRender = useRef(true);

  const colors = {
    1: [255, 99, 71],
    2: [100, 149, 237],
    3: [34, 139, 34],
    4: [255, 215, 0],
    5: [255, 223, 186],
    6: [255, 0, 255],
    7: [255, 165, 0],
    8: [0, 255, 255],
    9: [255, 255, 255],
    10: [88, 53, 171],
    11: [108, 58, 210],
    12: [40, 183, 81],
    13: [121, 9, 238],
    14: [81, 100, 77],
    15: [5, 184, 72],
    16: [210, 7, 111],
    17: [14, 74, 35],
    18: [21, 173, 60],
    19: [26, 140, 225],
    20: [57, 34, 1],
    21: [249, 110, 8],
    22: [104, 148, 102],
    23: [90, 220, 126],
    24: [8, 141, 108],
    25: [80, 159, 8],
    26: [55, 156, 214],
    27: [236, 11, 166],
    28: [195, 36, 220],
    29: [197, 240, 185],
    30: [75, 245, 81],
    31: [104, 69, 102],
    32: [173, 153, 145],
    33: [107, 152, 14],
    34: [157, 234, 151],
    35: [205, 243, 114],
    36: [202, 251, 111],
    37: [166, 142, 230],
    38: [40, 187, 31],
    39: [64, 93, 32],
    40: [200, 230, 151],
    41: [95, 83, 200],
    42: [198, 239, 221],
  };

  // @ts-ignore
  const { StudyInstanceUIDs } = useImageViewer();
  // @ts-ignore
  const [{ activeViewportId, viewports }, viewportGridService] = useViewportGrid();
  const {
    cornerstoneViewportService,
    hangingProtocolService,
    uiNotificationService,
    segmentationService,
  } = servicesManager.services;

  /*const computeHistogram = () => {
        const firstVp = cornerstoneViewportService.getCornerstoneViewport("default");
        const imageData = firstVp.getImageData().imageData;
        const scalars = imageData.getPointData().getScalars().getData()

        const histogram = new Array(3596).fill(0);
        for (const value of scalars) {
            if (value >= -500) {
                histogram[value - 500] += 1;
            }
        }
        const nbValues = histogram.reduce((a, b) => a + b)
        const percentStep = nbValues / 100

        let curPercentile = 1;
        let cumulatedSum = 0;
        histogram.forEach((value, index) => {
            cumulatedSum += value;
            while (cumulatedSum > percentStep * curPercentile) {
                percentileValues[curPercentile] = index - 500;
                curPercentile += 1;
            }
        });
    }*/

  useEffect(() => {
    // Change view to default
    const state = viewportGridService.getState();
    const { protocol } = hangingProtocolService.getActiveProtocol();
    if (protocol && protocol.id !== 'mprAnd3DVolumeViewport') {
      commandsManager.runCommand('setHangingProtocol', {
        protocolId: 'mprAnd3DVolumeViewport',
        stageIndex: 0,
      });
    }
  }, []);

  useEffect(() => {
    // TODO display when switching between Panoramic and 3D Edition
    if (isFirstRender.current) {
      isFirstRender.current = false;
      return;
    }

    const vp = Array.from(viewports.values()).find(
      vp => vp.viewportOptions.viewportType === 'volume3d'
    );
    console.assert(vp, 'UseEffect Threshold: Could not find "volume3d" viewport');
    viewportGridService.setActiveViewportId(vp.viewportId);

    const cornerstoneVP = cornerstoneViewportService.getCornerstoneViewport(vp.viewportId);
    const actorEntries = cornerstoneVP.getActors();
    if (!actorEntries || !actorEntries.length) {
      uiNotificationService.show({
        title: `Edition 3D export`,
        message: `No actor on 3d viewport`,
        type: 'error',
        duration: 3000,
      });
      return;
    }
    const ctfun = vtkColorTransferFunction.newInstance();
    ctfun.addRGBPoint(-1000, 0, 0, 0);
    ctfun.addRGBPoint(+skinThreshold, 1, 0, 0.031);
    ctfun.addRGBPoint(+boneThreshold, 1, 0.458, 0.105);
    ctfun.addRGBPoint(+toothThreshold, 1, 0.882, 0.207);
    ctfun.addRGBPoint(3095, 1, 1, 1);

    const ofun = vtkPiecewiseFunction.newInstance();
    ofun.addPoint(-1000, 0);
    ofun.addPoint(+skinThreshold, 0.05);
    ofun.addPoint(+boneThreshold, 0.3);
    ofun.addPoint(+toothThreshold, 1);
    ofun.addPoint(3095, 1);

    /*actorEntries.forEach(obj => {
            const actor = obj.actor;
            actor.getProperty().setRGBTransferFunction(0, ctfun);
            actor.getProperty().setScalarOpacity(0, ofun);
        });*/
    getEnabledElement(cornerstoneVP.element).viewport.render();
  }, [skinThreshold, boneThreshold, toothThreshold]);

  const focusOn = async () => {
    // Focus on viewport index 1, which is volume3D (if layout 2x2)
    // If length <= 1, means that focus is on and need to de-focus
    const vp = Array.from(viewports.values()).find(
      vp => vp.viewportOptions.viewportType === 'volume3d'
    );
    console.assert(vp, 'FocusOn: Could not find "volume3d" viewport');
    const activeViewportId = viewports.size > 1 ? Array.from(viewports.keys())[1] : 'default';
    viewportGridService.setActiveViewportId(activeViewportId);

    //Call to layout 1x1 on volume3d viewport
    commandsManager.runCommand('toggleOneUp', {});
  };

  const exportSTL = () => {
    // TODO Cache currently not filled
    // Get STL from actor in volume3d viewport
    const vp = Array.from(viewports.values()).find(
      vp => vp.viewportOptions.viewportType === 'volume3d'
    );
    console.assert(vp, 'Export STL: Could not find "volume3d" viewport');
    const cornerstoneVP = cornerstoneViewportService.getCornerstoneViewport(vp.viewportId);
    const actorEntries = cornerstoneVP.getActors();
    if (!actorEntries || !actorEntries.length) {
      uiNotificationService.show({
        title: `Edition 3D export`,
        message: `STL needs to be generated`,
        type: 'info',
        duration: 3000,
      });
      return;
    }
    for (const actorEntry of actorEntries) {
      const { uid: actorUID, actor } = actorEntry;
      if (actorUID === 'outline') {
        continue;
      }

      const polyData = actor.getMapper().getInputData();
      const fileContents = vtkSTLWriter.writeSTL(polyData, FormatTypes.BINARY);
      const blob = new Blob([fileContents], { type: 'text/plain;charset=utf-8' });
      const a = window.document.createElement('a');
      a.href = window.URL.createObjectURL(blob);
      a.download = `${actorUID}STL.stl`;
      a.text = `Download ${actorUID} STL`;
      document.body.appendChild(a);
      a.click();
      console.log(`${actorUID} polygons successfully exported`);
    }
  };

  const importSTL = async () => {
    // Prompt user to select a folder
    const input = document.createElement('input');
    input.type = 'file';
    input.webkitdirectory = true;
    input.multiple = true;
    input.accept = '.stl';
    input.style.display = 'none';
    document.body.appendChild(input);

    input.onchange = async (e: any) => {
      const files = Array.from(e.target.files).filter((file: File) =>
        file.name.toLowerCase().endsWith('.stl')
      );
      if (files.length === 0) {
        uiNotificationService.show({
          title: 'Import STL',
          message: 'No STL files found in selected folder.',
          type: 'info',
          duration: 3000,
        });
        return;
      }
      let actorEntries = [];
      for (const file of files) {
        // Assuming name format: poly_{toothNumber}_{polygonNumber}_STL.stl
        const label = file.name.split('_')[1];
        const i = file.name.split('_')[2];

        const arrayBuffer = await file.arrayBuffer();
        // Parse STL file
        const reader = vtkSTLReader.newInstance();
        reader.parseAsArrayBuffer(arrayBuffer);
        const polyData = reader.getOutputData(0);
        polyData.buildLinks();

        // Needed to have separate colors for surfaces
        const extractedPolyData = vtkPolyData.newInstance();
        extractedPolyData.getPoints().setData(new Float32Array(polyData.getPoints().getData()), 3);
        extractedPolyData.getPolys().setData(new Uint32Array(polyData.getPolys().getData()));
        extractedPolyData.getPolys().setNumberOfComponents(3);

        // Create actor and display
        const mapper = vtkMapper.newInstance();
        mapper.setInputData(extractedPolyData);
        const actor = vtkActor.newInstance();
        actor.setMapper(mapper);

        actor
          .getProperty()
          .setColor(colors[label][0] / 255, colors[label][1] / 255, colors[label][2] / 255);
        if (label === 3 || label === 10) {
          // Nerve or pulp
          actor.getProperty().setOpacity(1.0);
        } else if (label === 1 || label === 2) {
          // Upper or lower jawbone
          actor.getProperty().setOpacity(0.8);
        } else {
          actor.getProperty().setOpacity(0.9);
        }
        actor.getProperty().setBackfaceCulling(false);
        actorEntries.push({ uid: `poly_${label}_${i}`, actor: actor });
      }
      _displayMarchingCubes(actorEntries);
    };

    input.click();
    document.body.removeChild(input);
  };

  const _dropPoints = (imageData, keepRatio = 0.5) => {
    const originalData = imageData.getPointData().getScalars();

    const mask = new Float32Array(originalData.getNumberOfTuples());
    for (let i = 0; i < mask.length; i++) {
      mask[i] = myRandom() < keepRatio ? 1 : 0;
    }
    const maskedData = originalData.getData().map((value, index) => value * mask[index]);
    const maskedDataArray = vtkDataArray.newInstance({
      numberOfComponents: originalData.getNumberOfComponents(),
      values: maskedData,
      name: originalData.getName(),
    });
    const maskedImageData = vtkImageData.newInstance();
    maskedImageData.setDimensions(imageData.getDimensions());
    maskedImageData.getPointData().setScalars(maskedDataArray);

    return maskedImageData;
  };

  const _interpolatePoints = (imageData, resizeScale = 0.5) => {
    const inputReslice = vtkImageReslice.newInstance();
    inputReslice.setInputData(imageData);
    const inputDimensions = imageData.getDimensions();
    const outputDimensions = [
      inputDimensions[0] * resizeScale,
      inputDimensions[1] * resizeScale,
      inputDimensions[2] * resizeScale,
    ];
    const outputSpacing = [
      imageData.getSpacing()[0] * (inputDimensions[0] / outputDimensions[0]),
      imageData.getSpacing()[1] * (inputDimensions[1] / outputDimensions[1]),
      imageData.getSpacing()[2] * (inputDimensions[2] / outputDimensions[2]),
    ];
    inputReslice.setOutputSpacing(outputSpacing);
    inputReslice.setOutputExtent([
      0,
      outputDimensions[0] - 1,
      0,
      outputDimensions[1] - 1,
      0,
      outputDimensions[2] - 1,
    ]);

    return inputReslice.getOutputData();
  };

  const _displayMarchingCubes = actorEntries => {
    const vp = Array.from(viewports.values()).find(
      vp => vp.viewportOptions.viewportType === 'volume3d'
    );
    console.assert(vp, 'DisplayMarchingCubes: Could not find "volume3d" viewport');

    viewportGridService.setActiveViewportId(vp.viewportId);
    const cornerstoneVP = cornerstoneViewportService.getCornerstoneViewport(vp.viewportId);
    let actors = cornerstoneVP.getActors();
    // Add surface to current visualization
    actors.push(...actorEntries);
    cornerstoneVP.setActors(actors);

    const cornestoneRenderer = cornerstoneVP.getRenderer();
    cornestoneRenderer.getRenderWindow().setNumberOfLayers(2);
    cornestoneRenderer.setUseDepthPeeling(true);
    cornestoneRenderer.setOcclusionRatio(0.1);
    cornestoneRenderer.setMaximumNumberOfPeels(100);
    cornestoneRenderer.getActiveCamera().setParallelProjection(true);
    cornestoneRenderer.resetCamera();

    getEnabledElement(cornerstoneVP.element).viewport.render();
  };

  const marchingCubes = () => {
    // TODO handle early click
    const firstVp = cornerstoneViewportService.getCornerstoneViewport('default');
    let imageData = firstVp.getImageData().imageData;

    // Create a mask to randomly select points
    const vp = Array.from(viewports.values()).find(
      vp => vp.viewportOptions.viewportType === 'volume3d'
    );
    console.assert(vp, 'MarchingCubes: Could not find "volume3d" viewport');

    // -------------------- PREPROCESSING --------------------
    console.log('Marching cubes - Preprocessing');

    // Reduce number of points, method 1
    // imageData = dropPoints(imageData, 0.15)

    // Reduce number of points, method 2
    // imageData = interpolatePoints(imageData, 0.15)

    let actorEntries = [];

    const contourValue = ctValue;
    const contourType =
      contourValue < skinThreshold
        ? 'airways'
        : contourValue < boneThreshold
          ? 'skin'
          : contourValue < toothThreshold
            ? 'bones'
            : 'teeth';

    if (contourType == 'airways') {
      // By cropping, we ensure the polygon mesh is done on the inner airways and not on the ambient air
      const imageDataCopy = vtkImageData.newInstance();
      imageDataCopy.shallowCopy(imageData);

      const cropFilter = vtkImageCropFilter.newInstance();
      cropFilter.setCroppingPlanes([0, 400, 50, 400, 75, 280]);
      cropFilter.setInputData(imageDataCopy);
      imageData = cropFilter.getOutputData();
    }

    // -------------------- MESHING --------------------
    console.log(`Marching cubes - Processing ${contourType}`);

    const polyExtractor = imageMarchingCubes.newInstance({
      contourValue: contourValue,
      //contourValueMax: 1200,
      computeNormals: false,
      mergePoints: true,
    });

    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    const segmentation = segmentationService
      .getSegmentations()
      .filter(segmentation => segmentation.displaySetInstanceUID === displaySetUID)[0];
    const { voxelManager } = segmentationService.getLabelmapVolume(segmentation.segmentationId);
    const segScalarData = voxelManager.getCompleteScalarDataArray();
    for (const segment of Object.values(segmentation.segments)) {
      if (!segment) {
        // Skip empty segments
        continue;
      }
      const segmentIdx = segment.segmentIndex;
      const imageDataCopy = vtkImageData.newInstance();
      imageDataCopy.shallowCopy(imageData);
      let scalarData = imageDataCopy.getPointData().getScalars().getData();
      for (let i = 0; i < scalarData.length; i++) {
        // Dummy value so that segmentations are correctly separated (TODO refacto)
        scalarData[i] = segScalarData[i] === segmentIdx ? 1000 : 0;
      }
      polyExtractor.setInputData(imageDataCopy);
      const polySTL = polyExtractor.getOutputData();
      polySTL.buildLinks();

      // -------------------- AIRWAYS SEPARATION --------------------
      console.log('Marching cubes - Separating airways');

      // Divide skin in separated polys according to linking
      const allPolys = new PolyDataExtractor(polySTL).extractGroups();
      allPolys.map((p, i) => {
        const mapper = vtkMapper.newInstance();
        mapper.setInputData(p);
        const actor = vtkActor.newInstance();
        actor
          .getProperty()
          .setColor(segment.color[0] / 255, segment.color[1] / 255, segment.color[2] / 255);
        actor.getProperty().setOpacity(1);
        actor.getProperty().setBackfaceCulling(false);

        actorEntries.push({ uid: `poly_${segmentIdx}_${i}`, actor: actor });

        // Export polygons
        /*const fileContents = vtkSTLWriter.writeSTL(p, FormatTypes.BINARY);
        const blob = new Blob([fileContents], { type: 'text/plain;charset=utf-8' });
        const a = window.document.createElement('a');
        a.href = window.URL.createObjectURL(blob);
        a.download = `poly_${label}_${i}_STL.stl`;
        a.text = `Download poly_${label}_${i} STL`;
        document.body.appendChild(a);
        a.click();
        console.log(`poly_${label}_${i} polygons successfully exported`)*/
      });
    }

    /* // Sort polys according to their size
        const polysSize = allPolys.map((p, i) => ({ index: i, size: p.getNumberOfCells() }));
        polysSize.sort((p1, p2) => p2.size - p1.size);

        //Select the N biggest polys EXCEPT the first one (skin surface) TODO
        const N = 10; // TODO
        const idxs = Array.from({ length: N }, (value, index) => index);
        const polysMaxSizeIndexes = idxs.map(i => polysSize[i]);

        const selectedPolys = polysMaxSizeIndexes.map(obj => allPolys[obj.index]);

        const targetReduction = Number(reductionPercentile) / 100;
        //Create actors from selected polys
        selectedPolys.map((p, i) => {
            let qPolys = p;
            if (targetReduction > 0. && targetReduction < 1.) {
                const quadricDecimator = new QuadricDecimator(p, targetReduction);
                qPolys = quadricDecimator.decimate();
            }

            const mapper = vtkMapper.newInstance();
            mapper.setInputData(qPolys);
            const actor = vtkActor.newInstance();
            actor.setMapper(mapper);

            actor.getProperty().setColor(myRandom(), myRandom(), myRandom());
            actor.getProperty().setOpacity(1);
            actor.getProperty().setBackfaceCulling(false);
            // actor.getProperty().setAmbient(1)

            actorEntries.push({ uid: `airway_${i}`, actor: actor });
        });

        // -------------------- OUTLINE --------------------
        const outlineData = vtkOutlineFilter.newInstance();
        outlineData.setInputData(polys);
        const mapOutline = vtkMapper.newInstance();
        mapOutline.setInputConnection(outlineData.getOutputPort());
        const outline = vtkActor.newInstance();
        outline.setMapper(mapOutline);
        outline.getProperty().setColor(1, 1, 0);
        outline.getProperty().setOpacity(1);
        actorEntries.push({ uid: 'outline', actor: outline });*/

    // Display
    console.log('Marching cubes - Display');
    _displayMarchingCubes(actorEntries);
  };
  // @ts-ignore
  return (
    <div
      style={{
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-evenly',
        height: '50%',
      }}
    >
      <Button onClick={focusOn}>{'Volume Zoom In / Out'}</Button>
      <p style={{ color: 'white', fontSize: 'larger' }}>{'Volume rendering'}</p>
      <div style={{ display: 'inline-flex', width: '100%', justifyContent: 'flex-start' }}>
        <p style={{ color: 'white' }}>{`Airway -> Skin threshold: ${skinThreshold}`}</p>
      </div>
      <input
        type="range"
        min="-1000"
        max={boneThreshold}
        value={skinThreshold}
        className="slider"
        id="myRange"
        step={'1'}
        onChange={e => setSkinThreshold(e.target.value)}
      />
      <div style={{ display: 'inline-flex', width: '100%', justifyContent: 'flex-start' }}>
        <p style={{ color: 'white' }}>{`Skin -> Bone threshold: ${boneThreshold}`}</p>
      </div>
      <input
        type="range"
        min={skinThreshold}
        max={toothThreshold}
        value={boneThreshold}
        className="slider"
        id="myRange"
        step={'1'}
        onChange={e => setBoneThreshold(e.target.value)}
      />
      <div style={{ display: 'inline-flex', width: '100%', justifyContent: 'flex-start' }}>
        <p style={{ color: 'white' }}>{`Bone -> Tooth threshold: ${toothThreshold}`}</p>
      </div>
      <input
        type="range"
        min={boneThreshold}
        max="3095"
        value={toothThreshold}
        className="slider"
        id="myRange"
        step={'1'}
        onChange={e => setToothThreshold(e.target.value)}
      />
      <p style={{ color: 'white', fontSize: 'larger' }}>{'Mesh rendering'}</p>
      <div style={{ display: 'inline-flex', width: '100%', justifyContent: 'flex-start' }}>
        <p style={{ color: 'white' }}>{`CT value for mesh: ${ctValue}`}</p>
      </div>
      <input
        type="range"
        min="-1000"
        max="3095"
        value={ctValue}
        className="slider"
        id="myRange"
        step={'1'}
        onChange={e => setCtValue(e.target.value)}
      />
      <div style={{ display: 'inline-flex', width: '100%', justifyContent: 'flex-start' }}>
        <p style={{ color: 'white' }}>{`Target Reduction: ${reductionPercentile}%`}</p>
      </div>
      <input
        type="range"
        min="0"
        max="100"
        value={reductionPercentile}
        className="slider"
        id="myRange"
        step={'1'}
        onChange={e => setReductionPercentile(e.target.value)}
      />
      <Button onClick={marchingCubes}>{'Marching cubes'}</Button>
      <Button onClick={exportSTL}>{'Export STL'}</Button>
      <Button onClick={importSTL}>{'Import STL'}</Button>
    </div>
  );
}
